export const getters = {
	itemByType(state) {
		return function(type) {
			// undopoint
			// redopoint + 1
			// point
			let point = state.point,
				len = state.acts.length,
				ret = false;
			switch (type) {
				case 'UNDO':
					if ((point === -1 && len === 0) &&
						(point === 0 && len > 0)) {
						return ret
					}
					break;
				case 'REDO':
					if (len - 1 > point) {
						ret = ++point
					} else {
						return ret
					}
					break;
			}
			return state.acts[point]
		}
	},
	// 
	isLastPoint(state) {
		let len = state.acts.length;
		let size = state.point + 1
		return len === size
	}
}


